import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { environment } from 'src/environments/environment';
import { HttpClient } from '@angular/common/http';
import { AuthService } from 'src/app/auth/services/auth.service';

@Injectable({
  providedIn: 'root'
})
export class ConversationService {


  chatSocket!: WebSocket;
  public socketConnection = new Subject<any>();
  onConversation: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);

  constructor( private http: HttpClient, private authservice: AuthService) { }

  initSocket(id:any) {
    const uuid: any = this.authservice.getLoggedInDetail().uuid;
    const chatSocketUrl: any = `${environment.socketUrl}/conversation/${id}/${uuid}`;
    // this.chatSocket = new WebSocket(this.chatSocketUrl)
    this.chatSocket = new WebSocket(chatSocketUrl);
    let self = this;
    this.chatSocket.addEventListener('open', function (event) {
      self.socketConnection.next('online');
      console.log('chat socket connected');
    });
    this.chatSocket.addEventListener('message', function (event) {
      var data = JSON.parse(JSON.parse(event.data).payload)
      self.onConversation.next(data);
    });
    this.chatSocket.addEventListener('close', function (event) {
      self.socketConnection.next('offline');
      console.log('chat socket disconnected');
    });
  }

  sendMessage(data:any) {
    this.chatSocket.send(data);
  }

  closeSocket() {
    this.chatSocket.close();
  }
}
