import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class RolepermissionService {

  baseUrl = environment.baseUrl;
  constructor(private _http:HttpClient) 
  {


  }

  createRolePermission(data:any)
  {
    return this._http.post(this.baseUrl+'/api/v1/admin/role-and-permissions/',data);
  }
  deletePermission(id:any){
    return this._http.delete(`${this.baseUrl}/api/v1/admin/role-and-permissions/${id}/`);
  }

  deleteRole(id:number) {
    return this._http.delete(`${this.baseUrl}/api/v1/admin/roles/delete/${id}`);
  }
  
  getRolePermission(params:any)
  {
    return this._http.get(this.baseUrl+"/api/v1/admin/role-and-permissions/", { params });
  }

  getRolePermissionId(id:any)
  {
    return this._http.get(`${this.baseUrl}/api/v1/admin/role-and-permissions/${id}/`);
  }


  updateRolePermission(data:any,id:any)
  {
    return this._http.put(`${this.baseUrl}/api/v1/admin/role-and-permissions/${id}/`,data);
  }

}
