import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class LogReportService {

  baseUrl = environment.baseUrl;
  constructor(private _http: HttpClient) {

  }

  getLogReports(params: any) {
    return this._http.get(this.baseUrl + "/api/v1/admin/logs", { params });
  }

  getLogReportsByID(params: any,uuid:string) {
    return this._http.get(`${this.baseUrl}/api/v1/admin/customer/timeline/${uuid}` , { params });
  }

  getCustomerLogReports(params: any) {
    return this._http.get(this.baseUrl + "/api/v1/customer/logs", { params });
  }

  getLogById(id: any) {
    return this._http.get(`${this.baseUrl}/api/v1/admin/logs/${id}`);
  }

  downloadInvoiceFile(data: any, filename = 'data') {
    // let csvData = this.ConvertToInvoiceCSV(data, ['title','description','user','email','ip_address','event','module','created_at']);
    let csvData = this.ConvertToInvoiceCSV(data, ['#', 'user', 'file_request', 'email', 'ip_address', 'event', 'module', 'credit_update_type', 'credit_update_description', 'created_at', 'title', 'description']);
    let blob = new Blob(['\ufeff' + csvData], { type: 'text/csv;charset=utf-8;' });
    let dwldLink = document.createElement("a");
    let url = URL.createObjectURL(blob);
    let isSafariBrowser = navigator.userAgent.indexOf('Safari') != -1 && navigator.userAgent.indexOf('Chrome') == -1;
    if (isSafariBrowser) {  //if Safari open in new window to save file with random filename.
      dwldLink.setAttribute("target", "_blank");
    }
    dwldLink.setAttribute("href", url);
    dwldLink.setAttribute("download", filename + ".csv");
    dwldLink.style.visibility = "hidden";
    document.body.appendChild(dwldLink);
    dwldLink.click();
    document.body.removeChild(dwldLink);
  }

  ConvertToInvoiceCSV(objArray: any, headerList: any) {
    let array = typeof objArray != 'object' ? JSON.parse(objArray) : objArray;
    let str = '';
    var row = '';
    let newHeaders = ['#', 'User Name', 'File Request', 'User Email', 'User ip', 'Event Type', 'Module', 'Type', 'User Description', 'Event Timestamp', 'Title', 'Description'];
    for (let index in newHeaders) {
      row += newHeaders[index] + ',';
    }
    row = row.slice(0, -1);
    str += row + '\r\n';
    for (let i = 0; i < array.length; i++) {
      let line = '';
      for (let index in headerList) {
        let head = headerList[index];
        line += array[i][head] + ',';
      }
      str += line + '\r\n';
    }
    return str;
  }


}
