import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { debounceTime, Subject } from 'rxjs';

@Component({
  selector: 'smart-search',
  templateUrl: './smart-search.component.html',
  styleUrls: ['./smart-search.component.scss']
})
export class SmartSearchComponent implements OnInit{
  onSearch = new Subject<string>();
  instantSearch = new Subject<string>();

  // Placeholder to be put in input
  @Input() placeholder:string = '';
  @Output('onSearch') search = new EventEmitter<string>();
  
  // Emits the text search by the user 
  enteredValue = '';

  ngOnInit(): void {
    // Search with 800milisecond delay after typing
    this.onSearch
    .pipe(debounceTime(800))
    .subscribe(searchKeywords => this.search.emit(searchKeywords))

    // Instantly search for keywords
    this.instantSearch
    .subscribe(searchKeywords => this.search.emit(searchKeywords))
  }

  reset() {
    this.enteredValue = '';
  }
  
}
