import { inject, Injectable } from '@angular/core';
import { TitleCasePipe } from '@angular/common';

@Injectable({
  providedIn: 'root'
})
export class LogParseService {
  titleCasePipe = inject(TitleCasePipe);

 

  parseDetails(item:any) {
    if(item.title === "Purchased Credits" || item.title === "Spent Credits updated.") {
      return this.generateDescription(item);
    }

    if(item.title.includes("Ticket Update")) {
      console.log(item,'item');
      return this.generateTicketDescription(item);
      item.description
      
    }

    if(item.event === "Credits updated manually") {
      return this.generateDescription(item);
    }

    
    if(item.event.toLowerCase().includes('profile updated')) {
      return this.generateDescription(item);
    }
    
    return item;
  }

  generateTicketDescription(item:any) {
    if(!(item.old_details && item.new_details)) return item;

    item.old_details = item.old_details.replace("<TicketCategory: ",'"').replace(">",'"')
    item.new_details = item.new_details.replace("<TicketCategory: ",'"').replace(">",'"')
    

    try {
      item.old_details = JSON.parse(this.parseFromPythonString(item.old_details));
      
    } catch (err) {
      console.error("Failed to parse old_details:", err);
      console.log(this.parseFromPythonString(item.old_details),'old details');
      
      return item;
    }

    try {
      item.new_details = JSON.parse(this.parseFromPythonString(item.new_details));
    } catch (err) {
      console.error("Failed to parse new_details:", err);
      return item;
    }


    const keys = Object.keys(item.old_details);
    let description = "";
    let vehicleDetailsDesc = "";
    let isVehicleDetailsUpdate = false;

    for(let i = 0; i < keys.length; i++) {

      // If old value is not same as new value then add it to description
      if(keys[i] === 'vehicle_details') {
        const vehicleDetailsKeys = Object.keys(item.old_details[keys[i]]);
        for(let j = 0; j < vehicleDetailsKeys.length; j++) {

          if(item.old_details.vehicle_details[vehicleDetailsKeys[j]] !== item.new_details.vehicle_details[vehicleDetailsKeys[j]]) {
            isVehicleDetailsUpdate = true;
            // Replace underscore with space then transform to titlecase
            const keyName = this.titleCasePipe.transform(vehicleDetailsKeys[j].replaceAll("_", " "));
    
            // Append to description
            
            let oldData = item.old_details.vehicle_details[vehicleDetailsKeys[j]];
            let newData = item.new_details.vehicle_details[vehicleDetailsKeys[j]];
              
            oldData = oldData ? `"${oldData}"` : `Blank`;
            newData = newData ? `"${newData}"` : `Blank`;

            vehicleDetailsDesc += `Updated <span class="key-name">${keyName}</span> from ${oldData} to ${newData} \n`
          }
        }
        
        

      } else if(item.old_details[keys[i]] !== item.new_details[keys[i]]) {

        // Replace underscore with space then transform to titlecase
        const keyName = this.titleCasePipe.transform(keys[i].replaceAll("_", " "));

        // Append to description
        description += `Updated <span class="key-name">${keyName}</span> from "${item.old_details[keys[i]]}" to "${item.new_details[keys[i]]}" \n`
      }
    }
    item.description = description;

    // If Vehicle details are updated only then add this description
    if(isVehicleDetailsUpdate) {
      item.vehicleDetailsDesc = `<span class="vehicle-detail-title">Vehicle Details</span>\n` + vehicleDetailsDesc;
    }
    return item

  }

  generateDescription(item:any) {
    if(!(item.old_details && item.new_details)) return item;
    // Data from API contains stringify object turned into a single quoted string so convert it to JSON
    try {
      item.old_details = JSON.parse(this.parseFromPythonString(item.old_details));
      
    } catch (err) {
      console.error("Failed to parse old_details:", err);
      console.log(item.old_details,':Old Details');
      
      return item;
    }
    
    try {
      item.new_details = JSON.parse(this.parseFromPythonString(item.new_details));
    } catch (err) {
      console.error("Failed to parse new_details:", err);
      return item;
    }

    const keys = Object.keys(item.old_details);
    let description = "";

    for(let i = 0; i < keys.length; i++) {

      // If old value is not same as new value then add it to description
      if(item.old_details[keys[i]] !== item.new_details[keys[i]]) {

        // Replace underscore with space then transform to titlecase
        const keyName = this.titleCasePipe.transform(keys[i].replaceAll("_", " "));

        // Append to description
        description += `Updated <span class="key-name">${keyName}</span> from "${item.old_details[keys[i]]}" to "${item.new_details[keys[i]]}" \n`
      }
    }
    item.description = description;
    return item
  }

  parseFromPythonString(str:string): string {
    if(!str?.length) return str;
    try {
      return str.replaceAll("'",'"').replaceAll("True","true").replaceAll("False","false").replaceAll("None",'""');
    } catch(err) {
      console.log(err);
    }
    return str
  }

      
}
