import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class TemplateCategoryService {

  baseUrl = environment.baseUrl;
  constructor(private _http:HttpClient) 
  {

  }

  getAllTempCategory()
  {
    return this._http.get(this.baseUrl+"/api/v1/admin/template-category/");
  }

  getTempCategory(params:any)
  {
    return this._http.get(this.baseUrl+"/api/v1/admin/template-category/", { params });
  }

  createTempCategory(data:any)
  {
    return this._http.post(this.baseUrl+'/api/v1/admin/template-category/',data);
  }

  updateTempCategory(data:any,id:any)
  {
    return this._http.put(`${this.baseUrl}/api/v1/admin/template-category/${id}/`,data);
  }

  deleteTempCategory(id:any)
  {
    return this._http.delete(`${this.baseUrl}/api/v1/admin/template-category/${id}/`);
  }
  
}
