import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { BehaviorSubject, Observable, shareReplay, tap } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class TicketService {

  baseUrl = environment.baseUrl;
  statusCount = new BehaviorSubject<any>(null);
  constructor(private _http:HttpClient) 
  {}

  getStatusCount(disableLoader = false) {
    let headers = new HttpHeaders();
    if(disableLoader) {
      headers = headers.append('X-Disable-Loader','true')
    }
    return this._http.get(`${this.baseUrl}/api/v1/admin/ticket/status/count`,{headers}).pipe(tap(val => {
      this.statusCount.next(val);
    }))
  }

  getTicket(params:any)
  {
    let searchEnable = false;
    if("search" in params) {
      searchEnable = true;
    }
    return this._http.get(this.baseUrl+`/api/v1/admin/ticket${searchEnable ? '/search' : '/'}`, { params });
  }

  getTicketForFileRequest(id:string) {
    return this._http.get(`${this.baseUrl}/api/v1/file/service/request/ticket/${id}`);
  }

  getTicketCountByCategory() {
    return this._http.get(`admin/ticket/status/count`)
    // .pipe(shareReplay)
  }

  getTicketOnFilter(params:any)
  {
    return this._http.get(this.baseUrl+"/api/v1/admin/ticket/filter_ticket", { params });
  }
  createTicket(data:any)
  {
    return this._http.post(this.baseUrl+'/api/v1/admin/ticket/',data);
  }

  updateTicket(data:any,id:any)
  {
    return this._http.put(`${this.baseUrl}/api/v1/admin/ticket/${id}/`,data);
  }

  deleteTicket(id:any)
  {
    return this._http.delete(`${this.baseUrl}/api/v1/admin/ticket/${id}/`);
  }


  getConversation(id:any)
  {
    return this._http.get(`${this.baseUrl}/api/v1/customer/conversation/${id}`);
  }

  getAttachments(ids:any,disableLoader = false)
  {
    let headers = new HttpHeaders();
    if(disableLoader) {
      headers = headers.append('X-Disable-Loader','true')
    }
    return this._http.get(`${this.baseUrl}/api/v1/customer/conversation/attachment/${ids}`,{ headers })
  }

  saveAttachments(ids:any,file:any)
  {
    return this._http.post(`${this.baseUrl}/api/v1/customer/conversation/attachment/${ids}`,file);
  }

  updateTicketStatus(id:any,data:any)
  {
    return this._http.post(this.baseUrl+`/api/v1/admin/update-ticket-history/${id}`,data);
  }
  updateFRTicketStatus(frStatus:any,ticketId:any)
  {
    return this._http.get(this.baseUrl+`/api/v1/admin/ticket/status_update/file_request?fr_status=${frStatus}&ticket_id=${ticketId}`);
  }



}
