import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class TemplateService {

  baseUrl = environment.baseUrl;
  constructor(private _http:HttpClient) 
  {

  }

  getTemplate(params:any)
  {
    return this._http.get(this.baseUrl+"/api/v1/admin/template/", { params });
  }

  createTemplate(data:any)
  {
    return this._http.post(this.baseUrl+'/api/v1/admin/template/',data);
  }

  updateTemplate(data:any,id:any)
  {
    return this._http.put(`${this.baseUrl}/api/v1/admin/template/${id}/`,data);
  }

  deleteTemplate(id:any)
  {
    return this._http.delete(`${this.baseUrl}/api/v1/admin/template/${id}/`);
  }

  getTemplateById(id:any)
  {
    return this._http.get(`${this.baseUrl}/api/v1/admin/template/${id}/`);
  }

}
