import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'fileSize'
})
export class FileSizePipe implements PipeTransform {
  
  transform(value: number, decimalPlaces: number = 3): string {
    if (!value || isNaN(value) || value === 0) {
      return 'NA';
    }
  
    if (value < 1024) {
      return `${value} bytes`; // Display bytes for sizes less than 1 KB
    }

    const mbSize = value / (1024 * 1024); // Convert bytes to MB
    // if (mbSize >= 10) {
    //   // Display in MB if size is 10 MB or more
    //   const formattedMB = mbSize; // Use Math.floor to remove decimals
    //   return `${this.formatNumber(parseFloat(formattedMB.toFixed(2)))} MB`;
    // } else {
      // Display in KB for sizes below 10 MB
      const kbSize = value / 1024;
      return `${this.formatNumber(parseFloat(kbSize.toFixed(2)))} KB`;
    // }
  }

  private formatNumber(value: number): string {
    return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  }
}